import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import './Community_events.css';

const Community_events = () => {
  const [events, setEvents] = useState([]);
  
  useEffect(() => {
    const fetchEvents = async () => {
      try {
        const response = await fetch('http://localhost:5000/api/community-events');
        if (!response.ok) {
          throw new Error('Failed to fetch events');
        }
        const data = await response.json();
        setEvents(data.events);
      } catch (error) {
        console.error(error);
      }
    };

    fetchEvents();
  }, []);

  return (
    <div className="community-projects-container">
      <h2>Community Projects</h2>
      <Link to="/create-event">
        <button className="create-event-button">Create Community Event</button>
      </Link>
      <div className="project-cards">
        {events.map((event) => (
          <div className="project-card" key={event._id}>
            <h3>{event.eventName}</h3>
            <p>{event.description}</p>
            <p><strong>Date:</strong> {new Date(event.date).toLocaleDateString()}</p>
            <p><strong>Time:</strong> {event.time}</p>
            <p><strong>Mode:</strong> {event.mode}</p>
            {event.mode === 'offline' && <p><strong>Location:</strong> {event.location}</p>}
            {event.mode === 'online' && <p><strong>Meeting ID:</strong> {event.meetingId}</p>}
            <button className="participate-button">Participate</button>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Community_events;
