import React, { useEffect, useState } from 'react';
import './MyProfile.css';
import profilePhoto from "./profile.png"
import Navbar from '../components/Navbar';
import Footer from '../components/Foooter';

const MyProfile = () => {
  const [userData, setUserData] = useState(null);
  const [error, setError] = useState('');

  const queryParams = new URLSearchParams(window.location.search);
  const email = queryParams.get('email');  // Ensure it matches the query param in the URL

  useEffect(() => {
    const fetchUserData = async () => {
      try {
        const response = await fetch(`http://localhost:5000/api/users?email=${email}`);
        if (!response.ok) {
          throw new Error('User not found');
        }
        const data = await response.json();
        console.log('API response:', data); // Log the API response for debugging
        setUserData(data.user);
      } catch (error) {
        console.error('Error fetching user data:', error); // Log the error for debugging
        setError(error.message);
      }
    };

    if (email) {
      fetchUserData();
    }
  }, [email]);

  if (error) {
    return <p className="error-message">Error: {error}</p>;
  }

  if (!userData) {
    return <p className="loading-message">Loading profile...</p>;
  }

  return (
    <>
    <Navbar />
    <div class="main-profile">
    <div className="profileImage">
      <img src={profilePhoto} alt="Profile-Photo"/>
    </div>
    <div className="profile-container">
      <h1 className="profile-name">{userData.firstName} {userData.middleName} {userData.lastName}</h1>
      <div className="profile-details">
        <p><strong>Email:</strong> {userData.email}</p>
        <p><strong>Date of Birth:</strong> {new Date(userData.dateOfBirth).toLocaleDateString()}</p>
        <p><strong>Mobile Number:</strong> {userData.mobileNumber}</p>
        <p><strong>Address:</strong> {userData.address}, {userData.pincode}</p>
        <p><strong>Country:</strong> {userData.country}</p>
        <p><strong>State:</strong> {userData.state}</p>
        <p><strong>Description:</strong> {userData.description}</p>
        <p><strong>Skills:</strong> {userData.skills.join(', ')}</p>
      </div>
    </div>
    </div>
    <Footer/>
    </>
  );
};

export default MyProfile;
