// export default UserDashboard;

import React, { useContext, useEffect, useState } from 'react';
import { UserContext } from '../Pages/UserContext';
import Navbar from '../components/Navbar';
import Footer from '../components/Foooter'; // Corrected typo in Footer import
import emailjs from 'emailjs-com'; // Ensure you've imported EmailJS
import './User_dashboard.css';

const UserDashboard = () => {
  const { userEmail } = useContext(UserContext);
  const [skills, setSkills] = useState([]);
  const [filteredSkills, setFilteredSkills] = useState([]);
  const [error, setError] = useState(null);
  const [credits, setCredits] = useState(1000); // Initialize user credits to 1000
  const [skillFilter, setSkillFilter] = useState(''); // State for skill filter

  // Fetch skills data from the backend
  useEffect(() => {
    const fetchSkills = async () => {
      try {
        const response = await fetch('http://localhost:5000/api/offer-skills');
        if (!response.ok) {
          throw new Error('Failed to fetch skills');
        }
        const data = await response.json();
        setSkills(data.skills);
        setFilteredSkills(data.skills); // Initially set all skills
      } catch (error) {
        setError(error.message);
      }
    };

    fetchSkills();
  }, []);

  // Filter skills based on dropdown selection
  const handleFilterChange = (e) => {
    const selectedSkill = e.target.value;
    setSkillFilter(selectedSkill);
    if (selectedSkill === '') {
      setFilteredSkills(skills); // Show all skills if no filter is selected
    } else {
      setFilteredSkills(skills.filter(skill => skill.skill === selectedSkill));
    }
  };

  // Handle join button click
  const handleJoin = async (skill) => {
    if (credits < skill.credits) {
      alert("Insufficient credits to join this skill.");
      return;
    }

    const registrationData = {
      userEmail,
      skillId: skill._id,
      skillName: skill.skill,
      meetingId: skill.meetingId, 
      password: skill.password,
      date: skill.date,
      time: skill.time,
      mode: skill.mode,
      address: skill.address,
      description: skill.description,
      credits: skill.credits
    };

    try {
      const response = await fetch('http://localhost:5000/api/my-registrations', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(registrationData),
      });

      if (!response.ok) {
        throw new Error('Failed to register for skill');
      }

      // Proceed with sending email after successful registration
      const emailData = {
        to_email: userEmail,
        skillName: skill.skill,
        date: skill.date,
        time: skill.time,
        meetingId: skill.meetingId,
        password: skill.password,
      };

      emailjs.send(
        'service_d8xbbj9',  // Replace with your EmailJS Service ID
        'template_7c9isgr', // Replace with your EmailJS Template ID
        emailData,
        'jWziZX8HuJNIMyhZX'      // Replace with your EmailJS User ID
      )
      .then((response) => {
        alert('Email sent successfully!');
      })
      .catch((error) => {
        console.error('Email send error:', error);
        alert('Failed to send email. Please try again.');
      });

      // Update state after successful registration
      setSkills((prev) => prev.filter((s) => s._id !== skill._id));
      setFilteredSkills((prev) => prev.filter((s) => s._id !== skill._id)); // Update filtered skills
      const updatedCredits = credits - skill.credits;
      setCredits(updatedCredits);
      alert(`You have joined ${skill.skill}. Remaining credits: ${updatedCredits}`);
    } catch (error) {
      alert(`Error: ${error.message}`);
    }
  };

  return (
    <div>
      <div id="header1" className="header-section1">
        <div className="intro1">
          <video autoPlay muted loop className="background-video1">
            <source
              src="https://videos.pexels.com/video-files/3209298/3209298-sd_640_360_25fps.mp4"
              type="video/mp4"
            />
            Your browser does not support the video tag.
          </video>
          <div className="overlay1">
            <div className="intro-content1">
              <h1>Discover, Share and Grow Your Skills!</h1>
              <p>Welcome to SKILL<span>x</span>CHANGE</p>
            </div>
          </div>
        </div>
      </div>

      <div className="dashboard-container1">
        <div className="intro-content1">
          <h1>Join Courses</h1>
        </div>
        <Navbar email={userEmail} />
        <div className="body1">
          {userEmail && <h2>User: {userEmail}</h2>}
          <p><strong>Credits:</strong> {credits}</p>
          {error && <p className="error-message">{error}</p>}

          {/* Skill Filter Dropdown */}
          <select className="skill-filter1" value={skillFilter} onChange={handleFilterChange}>
            <option value="">All Skills</option>
            {skills.map(skill => (
              <option key={skill._id} value={skill.skill}>
                {skill.skill}
              </option>
            ))}
          </select>

          <div className="skills-list1">
            {filteredSkills.length === 0 && !error && <p>No skills available.</p>}
            {filteredSkills.map((skill) => (
              <div className="skill-card1" key={skill._id}>
                <h3>{skill.skill}</h3>
                <p><strong>Date:</strong> {new Date(skill.date).toLocaleDateString()}</p>
                <p><strong>Time:</strong> {skill.time}</p>
                <p><strong>Description:</strong> {skill.description}</p>
                <p><strong>Mode:</strong> {skill.mode}</p>
                {skill.mode === 'offline' && <p><strong>Address:</strong> {skill.address}</p>}
                <p><strong>Credits:</strong> {skill.credits}</p>
                <button className="join-button1" onClick={() => handleJoin(skill)}>
                  Join
                </button>
              </div>
            ))}
          </div>
        </div>
        <Footer />
      </div>
    </div>
  );
};

export default UserDashboard;
